import React from "react";
import { Input, Modal  , Radio , Form } from "antd";
import { SaveFilled } from "@ant-design/icons";
import IntlMessages from "util/IntlMessages";
import { FormattedMessage } from "react-intl";

const RadioGroup = Radio.Group;

const options = [
  {label: 'ذكر', value: 'male'},
  {label: 'أنثى', value: 'female'},
];
class AddStudent extends React.Component {

  state = {
    name :'',
    mobile :'',
    gender :'',
    bankiban:'',
    bankname:'',
    password :'',
  }

  render() {
    const { onAddStudent, onToggleModal, open } = this.props;
    const { name , mobile ,gender , password , bankiban , bankname } = this.state;
   
    return (
      <Modal
        okText={<SaveFilled />}
        cancelText={ <IntlMessages id="modal.cancelSave"/> }
        title={<IntlMessages id="student.addStudent"/>}
        toggle={onToggleModal} visible={open}
        closable={false}
        onOk={() => {
          if (name === '' || mobile === '' || gender === '' || password === '')
            return;
          onToggleModal("addStudentState");
          onAddStudent({ name , mobile , gender , bankiban , bankname, password });
          this.setState({ name: '' , mobile : '' ,  gender : '' , password : '' ,  bankname : '' ,  bankiban : '' })
        }}
        onCancel={()=> {
          onToggleModal("addStudentState")
          this.setState({ name: '' , mobile : '' ,  gender : '' , password : '' ,  bankname : '' ,  bankiban : '' })
        }}>

        <div  className="gx-modal-box-row">
          <div className="gx-modal-box-form-item">
          <Form labelCol={{span:6}} wrapperCol={{span:14}}>
            <div className="gx-form-group">
              <FormattedMessage id="columns.name" defaultMessage="name">
              {
                placeholder => (
                  <Form.Item rules={[{ required: true }]}   label={placeholder}>
                    <Input
                    required
                    value={name}
                    placeholder={placeholder}
                    onChange={(event) => this.setState({name: event.target.value})}
                    margin="none"/>
                   </Form.Item>
                  )
                }
            </FormattedMessage>
            </div>
            <div className="gx-form-group">
              <FormattedMessage id="columns.mobile" defaultMessage="name">
              {
                placeholder => (
                  <Form.Item rules={[{ required: true }]}   label={placeholder}>
                  <Input
                  required
                  value={mobile}
                  placeholder={placeholder}
                  onChange={(event) => this.setState({mobile: event.target.value})}
                  margin="none"/>
                  </Form.Item>
                  )
                }
            </FormattedMessage>
            </div>
            
            
            <div className="gx-form-group">
              <FormattedMessage id="columns.bankname" defaultMessage="bankname">
              {
                placeholder => (
                  <Form.Item rules={[{ required: true }]}   label={placeholder}>
                  <Input
                  required
                  value={bankname}
                  placeholder={placeholder}
                  onChange={(event) =>  this.setState({bankname: event.target.value})}
                  margin="none"/>
                  </Form.Item>
                  )
                }
            </FormattedMessage>
            </div>

            <div className="gx-form-group">
              <FormattedMessage id="columns.bankiban" defaultMessage="bankiban">
              {
                placeholder => (
                  <Form.Item rules={[{ required: true }]}   label={placeholder}>
                  <Input
                  required
                  value={bankiban}
                  placeholder={placeholder}
                  onChange={(event) =>  this.setState({bankiban: event.target.value})}
                  margin="none"/>
                  </Form.Item>
                  )
                }
            </FormattedMessage>
            </div>

            <div className="gx-form-group">
              <FormattedMessage id="columns.password" defaultMessage="name">
              {
                placeholder => (
                  <Form.Item rules={[{ required: true }]}   label={placeholder}>
                  <Input
                  required
                  value={password}
                  placeholder={placeholder}
                  onChange={(event) => this.setState({password: event.target.value})}
                  margin="none"/>
                  </Form.Item>
                  )
                }
            </FormattedMessage>
            </div>
            <div className="gx-form-group">
              <Form.Item label={<IntlMessages id="columns.gender"/>}>
                 <RadioGroup  onChange={(e)=> {
                   console.log(e.target.value)
                   this.setState({gender: e.target.value})
                 }} options={options} />
              </Form.Item>
            </div>
            </Form>
          </div>
        </div>
      </Modal>
    );
  }
}

export default AddStudent;
